
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Previewer from 'vux/src/components/previewer/index.vue'

export default {
props: ['card', 'chunkedImages', 'images'],
data () {
return {
showPreview: false,
previewIndex: 0
}
},
components: {
Swiper, Previewer
},
computed: {
imageUrls () {
return this.images.map(item => {
return {
msrc: item.src,
w: '100'
}
})
},
imgList () {
return this.images.map(item => {
return {
img: item.src
}
})
}
},
methods: {
viewImage (row, index) {
console.log('viewImage', this.imgList)
this.showPreview = true
this.previewIndex = row * 3 + index
},
stopPreview () {
this.showPreview = false
}
}
}



